.App {
    text-align: center;
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        animation: App-logo-spin infinite 20s linear;
    }
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

.Configs {
    display: flex;
}

.CharactersConfig, .WeaponsConfig, .PowerUpsConfig {
    border: solid 1px black;
}

.Characters, .Weapons, .PowerUps {
    display: flex;
    flex-direction: row;
}

.CharactersHeader, .WeaponsHeader, .PowerUpsHeader {
    display: flex;
    flex-direction: row;
}

.Header-Unlocked, .Header-Bought {
    width: 5rem;
    display: flex;
    justify-content: center;
}
fieldset{
    min-width: 0;
    text-overflow: initial;
    overflow: scroll;
}